
import { Container } from '@mui/system';
import axios from 'axios';
import { useEffect, useState } from 'react';
import './App.css';
import Tempapp from './components/Tempapp';
import WReport from './components/WReport';

function App() {
const [city, setCity] = useState("")
const [weather, setWeather] = useState([])
const [main, setMain] = useState([])
const [weat, setWeat] = useState([])
const [sys, setSys] = useState([])
const [wind, setWind] = useState([])

const weatherApi = async(url) => { 
   try {
    const data = await axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${city}&units=metric&appid=ac90e3b6f29dcc7fb48770cb36c48d89`);
    setWeather(data.data);
    console.log(data.data);
    setMain(data.data.main);
    setWeat(data.data.weather);
    setSys(data.data.sys);
    setWind(data.data.wind)
  } catch (error) {
    // console.log(error);
  }
}

useEffect(() => {
  weatherApi();
}, [city])




  return (
    <div className="App" style={{height: "100vh"}}>
      <Container
      maxWidth="md"
      style={{
        display: "flex",
        flexDirection: "column",
        height: "100vh",
      }}
      >
        <Tempapp city={city}
          setCity={setCity}/>
          <WReport weather={weather} city={city} weat={weat} main={main} sys={sys} wind={wind}/>
      </Container>
    </div>
  );
}

export default App;
