import React from "react";
import "./TableDesin.css"

const TableDesig = ({description, data, text}) => {
  // console.log(text);
  return (
    <div className="table-disign">
      <p>{description}</p>
      <p className="dub">{data} <span>{text}</span></p>
    </div>
  );
};

export default TableDesig;
