import React from "react";
import TableDesig from "./TableDesig";
import "./WReport.css";
// import { DateTime } from "luxon";

const WReport = ({ weather, city, weat, main, sys, wind }) => {
  const img1 = `https://openweathermap.org/img/wn/${weat.map(
    (weath) => weath.icon
  )}@2x.png`;

  function getDate() {
    const days = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday",
    ];
    const months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];
    let d = new Date();
    let day = `${days[d.getDay()]}, ${months[d.getMonth()]} ${d.getDate()}`;
    return day;
  }

  const redbg = {
    backgroundImage: "linear-gradient(120deg, #f6d365 0%, #fda085 100%"
  }
  const bluebg = {
    backgroundImage: "linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%"
  }

  const bgcolor = (Math.floor(main.temp) > 16) ? redbg : bluebg; 

  const format = "cccc, dd LLL yyyy' | Local time: 'hh:mm a";
  // console.log(bgcolor);

  // const {temp} = weather.main;
  // console.log(weather.main.temp);

  return (
    <>
      {city ? (
        <div className="report">
          <div className="current-wed">
            <div className="con-dev" style={bgcolor}>
              <div className="title-wed">
                <p className="dub ">
                  {weather.name}, <span>{sys.country}</span>
                </p>
                <h1>Current Weather</h1>
                {/* <p className="dub">{DateTime.fromSeconds(weather.dt).setZone(weather.timezone).toFormat(format)}</p> */}
                <p className="dub">{getDate()}</p>
              </div>
              <div className="degree">
                <div className="dgree-in">
                  <div className="dgree-in-con">
                    <img src={img1} alt="cloude" />
                  </div>
                  <div className="">
                    <h1 style={{ fontSize: "4.5rem" }}>
                      {Math.floor(main.temp)}°<span>C</span>
                    </h1>
                  </div>
                </div>
                <p className="dgree-tit">
                  {weat.map((weath) => weath.description)}
                </p>
              </div>
            </div>

            {/* <p className="dgree-tit">Mostly clear</p> */}
            <div className="table-info " style={bgcolor}>
              <div className="table-info-in">
                <TableDesig
                  description="feels like"
                  data={Math.floor(main.feels_like)}
                  text="°C"
                />
                <TableDesig
                  description="Min temp"
                  data={Math.floor(main.temp_min)}
                  text="°C"
                />
                <TableDesig
                  description="Max temp"
                  data={Math.floor(main.temp_max)}
                  text="°C"
                />
              </div>
              <div className="table-info-in">
                <TableDesig
                  description="Humidity"
                  data={main.humidity}
                  text="%"
                />
                <TableDesig
                  description="Pressure"
                  data={main.pressure}
                  text="hPa"
                  />
                <TableDesig
                  description="visibility"
                  data={Math.floor(weather.visibility / 1000)}
                  text="km"
                  />
              </div>
            </div>


            <p className="dgree-tit" style={{fontSize: "1rem"}}>Atmospheric pressure</p>
            <div className="table-info " style={bgcolor}>
              <div className="table-info-in">
                <TableDesig
                  description="sea level"
                  data={main.sea_level}
                  text="hPa"
                />
                
              </div>
              <div className="table-info-in">
                <TableDesig
                  description="ground level"
                  data={main.grnd_level}
                  text="hPa"
                />
                
              </div>
            </div>

            <p className="dgree-tit" style={{fontSize: "1rem"}}>Wind </p>
            <div className="table-info " style={bgcolor}>
              <div className="table-info-in">
                <TableDesig
                  description="Wind speed"
                  data={wind.speed}
                  text="meter/sec"
                />
                
              </div>
              <div className="table-info-in">
                <TableDesig
                  description="Wind gust"
                  data={wind.gust}
                  text="meter/sec"
                />
                
              </div>
            </div>

          </div>

          <p className="developer">
            Developed by <b>Madhubabu Akkishetty</b>
          </p>
        </div>
      ) : (
        <div className="subtitle">start by typing</div>
      )}
    </>
  );
};

export default WReport;
