import { Search } from "@mui/icons-material";
import { Button, TextField } from "@mui/material";
import React, { useState } from "react";
import "./Style.css";

const Header = ({ city, setCity }) => {
  // console.log(city);
  const [cityHamhle, setHityHamhle] = useState("")

  const onFormSubmit = e => {
    e.preventDefault();
    // send state to server with e.g. `window.fetch`
    // console.log(e);
    setCity(cityHamhle);
  }

  return (
    <div className="header">
      {/* <span className="title">{word? word: "word hunt"}</span> */}
      {/* <h1 style={{color:"#302e2e"}}>Weather App</h1> */}
      <div className="inputs">
        <form onSubmit={onFormSubmit}>
          
          <TextField
            className="search"
            label="search a city"
            id="standard-basic"
            variant="outlined"
            color="success"
            value={cityHamhle}
            onChange={(e) => setHityHamhle(e.target.value)}
          />
          {/* <Button variant="outlined" endIcon={<Search />}></Button> */}
        </form>
      </div>
    </div>
  );
};

export default Header;
